<template>
	<div id="login">
	  <span>姓名:</span><input type="text" v-model="name" @change="setName">
	</div>
</template>

<script type="text/javascript">
  export default{
  	props:['title'],
  	data(){
  		return {
  			name:"",
  		}
  	},
  	methods:{
  		setName(){
  			this.$emit("setName",this.name);
  		}
  	}
  }
</script>

<style lang="less" type="text/css" scoped>
	#login{
		display: flex;
		span{
			width:100px;
		}
		input{
			flex:1;
		}
	}
</style>


