<template>
	<div id="nav">
	  <ul>
	    <router-link :key="v.name" v-for="(v,index) in title" tag="li" :to="v.url">{{v.name}}</router-link>
	  </ul>
	</div>
</template>

<script type="text/javascript">
  export default{
  	data(){
  		return {
  			
  		}
  	},
  	props:['title'],
  }
</script>

<style lang="less" type="text/css" scoped>
	#nav{
		width:100%;
		color:#666;
		ul{
			border-bottom:1px solid #dedede;
			overflow: hidden;
			display:flex;
			li{
				flex:1;
				text-align: center;
				height:88px;
				line-height: 88px;
				border-right:1px solid #dedede;
				&:last-child{
					border-right:none;
				}
				&.router-link-active{
					color:blue;
				}
			}
		}
	}
</style>


