<template>
  <div id="page">
  	游戏
  	{{msg}}
	{{name}}
  	<Login @setName="setName"/>

  	<div class="cs"></div>
  </div>
</template>

<script type="text/javascript">

 import Login from "@/components/login"
  
 export default{
 	data(){
 		return {
 			msg:"",
 			name:"",
 		}
 	},
 	methods:{
 		setName(name){
 			this.name = name;
 		}
 	},
 	created(){
 		this.$store.commit('increment');
 		this.msg = this.$store.state.count;
 	},
 	components:{
 		Login,
 	}
 }

</script>

<style type="text/css" scoped>
  	.cs{
  		width:200px;
  		height:200px;
  		position:fixed;
  		bottom:0;
  		left:50%;
  		margin-left:-50px;
  		background: blue;
  	}
</style>


