<template>
  <div id="page">
  	首页
  	
  </div>
</template>

<script type="text/javascript">
  
 export default{
 	data(){
 		return {
 			msg:"",
 		}
 	},
 	methods:{
 		getList(){

 			let data = {
 				os: "ios",
				for_mobile: 1,
				callback: "jsonp1",
				start: 0,
				count: 18,
				loc_id: 108288,
				_: 0,
 			} 

 			this.$http.get("/api/rexxar/api/v2/subject_collection/movie_showing/items?os=ios&for_mobile=1&callback=jsonp1&start=0&count=18&loc_id=108288&_=0").then((res)=>{
 				console.log(res);
 			})
 		}
 	},
 	created(){
 		this.$store.commit('increment');
 		this.msg = this.$store.state.count;
 		this.getList();
 	}
 }

</script>

